.mc-tooltip {
    text-align: left;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #100010;
    background-color: rgba(16, 0, 16, 0.94);
    color: #FFF;
    padding: 6px;
    font-family: Minecraft;
    font-size: 16px;
    word-spacing: 4px;
    white-space: nowrap;
    line-height: 20px;
    text-shadow: 2px 2px 0 #3F3F3F;
    margin: 2px 4px;
    pointer-events: none;
    z-index: 99;
}

.mc-tooltip:before {
    content: "";
    position: absolute;
    top: 2px;
    right: -2px;
    bottom: 2px;
    left: -2px;
    border-style: none solid;
    border-color: rgba(16, 0, 16, 0.94);
}

.mc-tooltip:after {
    content: "";
    position: absolute;
    top: 2px;
    right: 0;
    bottom: 2px;
    left: 0;
    border: 2px solid #2D0A63;
    -webkit-border-image: -webkit-linear-gradient(rgba(80, 0, 255, 0.31), rgba(40, 0, 127, 0.31)) 1;
            border-image: linear-gradient(rgba(80, 0, 255, 0.31), rgba(40, 0, 127, 0.31)) 1;
}

.mc-tooltip > .mc-tooltip-description {
    font-size: 12px;
    display: block;
    margin-top: 4px
}

/* Begin react context menu stuff */

.react-contextmenu {
    min-width: 160px;
    padding: 5px 0;
    margin: 2px 0 0;
    font-size: 16px;
    color: #373a3c;
    text-align: left;
    background-color: #fff;
    background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, .15);
    border-radius: 4px;
    outline: none;
    opacity: 0;
    pointer-events: none;
    box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
    box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
}

.react-contextmenu.react-contextmenu--visible {
    opacity: 1;
    pointer-events: auto;
}

.react-contextmenu-item {
    padding: 3px 20px;
    font-weight: normal;
    line-height: 1.5;
    color: #333;
    text-align: inherit;
    white-space: nowrap;
    background: 0 0;
    border: 0;
    cursor: pointer;
}

.react-contextmenu-item.react-contextmenu-item--active,
.react-contextmenu-item:hover {
    color: #262626;
    background-color: #f5f5f5;
    border-color: #f5f5f5;
    text-decoration: none;
}

.react-contextmenu-item.react-contextmenu-item--disabled,
.react-contextmenu-item.react-contextmenu-item--disabled:hover {
    color: #878a8c;
    background-color: transparent;
    border-color: rgba(0, 0, 0, .15);
}

.react-contextmenu-item--divider {
    margin-bottom: 3px;
    padding: 2px 0;
    border-bottom: 1px solid rgba(0, 0, 0, .15);
    cursor: inherit;
}

.react-contextmenu-item--divider:hover {
    background-color: transparent;
    border-color: rgba(0, 0, 0, .15);
}
.crafting {
    max-width: 230px;
    margin: 0 auto;
}

.crafting-table {
    background: #C6C6C6;
    text-align: left !important;
    margin-left: 2px;
    line-height: 42px;
}

.grid, .grid-large, .grid-furnace, .grid-stonecutter {
    float: left;
    display: block;
    text-align: center !important;
    background-color: #8B8B8B;
    border: 2px solid #373737;
    border-right-color: #FFF;
    border-bottom-color: #FFF;
    vertical-align: middle;
}

.grid-large img, .grid-stonecutter img {
    z-index: -1;
    margin-top: 8px;
}

.grid, .grid-furnace {
    width: 36px;
    height: 36px;
}

.grid:hover, .grid-large:hover, .grid-furnace:hover, .grid-stonecutter:hover {
    background-color: rgba(255, 255, 255, 0.4);
}

.grid-large, .grid-stonecutter {
    width: 48px;
    height: 48px;
}

.grid-tag {
    background-color: #7a7162;
}

.grid-furnace {
    margin-left: 50px;
}

.grid-stonecutter {
    margin-top: 20px;
}

.grid img, .grid-large img, .grid-furnace img, .grid-stonecutter img {
    cursor: move;
}

.crafting .recipe {
    float: left;
    width: 110px;
}

