}

.crafting .flame {
    width: 32px;
    height: 32px;
    margin-left: 50px;
    margin-bottom: 5px;
    float: left;
    background: url(data:image/png;base64,);
}

.crafting-table-output {
    float: left;
    padding-top: 40px;
}

/* only for small */
@media screen and (max-device-width: 400px) {
    .crafting .arrow {
        margin-left: 0;
        margin-right: 0;
    }
}

.recipe > h6 {
