}

.download-button {
    margin-top: 10px;
}

@media screen and (max-device-width: 400px) {
    .download-button {
        overflow: hidden;
    }
}

@-webkit-keyframes shake {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    10% {
        -webkit-transform: rotate(-5deg);
                transform: rotate(-5deg);
    }
    20% {
        -webkit-transform: rotate(5deg);
                transform: rotate(5deg);
    }
    30% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    40% {
        -webkit-transform: rotate(5deg);
                transform: rotate(5deg);
    }
    50% {
        -webkit-transform: rotate(-5deg);
                transform: rotate(-5deg);
    }
    60% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    70% {
        -webkit-transform: rotate(-5deg);
                transform: rotate(-5deg);
    }
    80% {
        -webkit-transform: rotate(5deg);
                transform: rotate(5deg);
    }
    90% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(-5deg);
                transform: rotate(-5deg);
    }
}

@keyframes shake {
